.App {
  width: 100vw;
  max-width: 900px;
  margin: auto;
  background:rgba(239, 239, 239, 0.7);
  ;}

.App-logo {
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  display: flex;
  flex-direction: column;
  top: 0;
  justify-content: center;
  /*font-size: calc(10px + 2vmin);*/
  background-color: #063773;
  text-align: center;
  color:#063773;
}
form{
  position: relative;
  border: rgb(244, 244, 244,0.5) 0.5px solid ;
  width: 50%;
  border-radius: 50px;
  margin: 5px auto 10px auto;
  transition: width 0.2s linear 0.3s cubic-bezier(0.645, 0.045, 0.355, 1);
  padding: 0 60px 0 5px;

}

#searchBtn{
  display:block;
  right: -0.2px;
  width: 1px;
  height: 0.1px;
  background-color: transparent;
  border: none;
  border-radius: 50px;
  transition: background-color 0.4s linear 0.08s;
  color: transparent;
  }

.search{
  padding:  auto;
  border-radius: 50px;
  height: 1.5rem;
  width: 100%;
  outline: 0;
  background: transparent;
  font-size: 1rem;
  font-weight: 700;
  line-height: 1;
  border: none ;
  flex-grow:2;
  color: rgb(218, 218, 218);
}

.search:not(:placeholder-shown:empty) + button#searchBtn{
  position: absolute;
  right: 0.05px;
  top: 0.5px;
  height: 95%;
  border:none;
  color: rgb(255, 255, 255);
  width:30px;
  font-size: 1rem;
  background: #14a051;
}
  
.search:focus{
  width: 100%;
}

.search::placeholder {
  color: rgba(255, 255, 255, 0.5);
}
.App-link {
  color: #61dafb;
}


@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
main{
  min-height: 85vh;
}
.productsContainer{
  display:flex;
  direction:column;
  justify-content:center;
  flex-wrap:wrap;
  margin-top: 20px;
  margin-bottom: 20px;
  background:transparent;
  width:100%;
  gap: 15px;
}

.card{
  padding: 5px;
  width: 40%;
  max-height: 200px;
  background:#FFF;
  border-radius: 5px;
  box-shadow: rgb(207, 207, 207,50%) 2px 2px 5px;
}

h3{
  text-align: center;
}

.products{
display: flex;
flex-direction: column;
padding: 1px 10px;
background:#FFF;
font-size: smaller;
}

.product_detail{
  margin: 10px 0;
}

 p{
  margin-top: 0;
  margin-bottom: 0;
}

.products img{
  margin: auto;
  padding: 5px 15px;
  width: 60%;
  max-width: 100px;
  max-height: 100px;
}

.categoryContainer{
display:flex;
direction:row;
justify-content:center;
flex-wrap: wrap;
width: 90%;
margin: 0 auto;
gap: 15px;
}
.categoryContainer article{
  display: flex;
  justify-content: center;
  width: 45%;
  max-width: 150px;
  cursor:pointer;
  padding: 0 auto;
  color: rgb(49, 49, 49);
  font-weight: 700;

}
.categoryContainer article p{
  margin: auto;
  font-size: smaller;
}
.categoryContainer article img{
  width: 20%;
  margin: 5px auto;
  background: rgb(255, 255, 255,35%);
  border-radius: 15px;
}
h4{
  color: rgb(255, 255, 255);
  text-shadow: #7f7f7f 0.7px 0.6px 0.5px;
  margin: 0;
  padding: 5px 0;
}

footer{
  
  position: sticky;
  bottom: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  /*font-size: calc(10px + 2vmin);*/
  height: 4vh;
  color: #063773;
}
footer ul{
  background: #FFF;
  width: 100%;
  list-style: none;
  display: flex;
  flex-direction: row;
  justify-content: center;
  gap: 60px;
  padding: 0 ;
}
footer ul img{
  width: 2rem;
}
.Home{
border-bottom: 10px #063773 solid;
}

